import styled from "styled-components";

export const Divteste = styled.div`
//position: relative;
`;

export const StyleMensagem = styled.div`
position: absolute;
width:100%;
height:100%;
top:18%;
@media (max-width: 768px) {
    top:22%;
    }
@media (max-width: 540px) {
    top:40%;
  
    }
`;

export const PrincipalText = styled.h1`
font-family: 'M PLUS 1p', sans-serif;
font-style: normal;
font-weight: bold;
font-size: 3rem;
line-height: 4rem;
text-align: center;
color: #FFFFFF;
@media (max-width: 1024px) {
    font-size: 2rem;
    line-height: 4rem;
    }
@media (max-width: 768px) {
    font-size: 1.5rem;
    line-height: 3.2rem;
    }
@media (max-width: 426px) {
    font-size: 0.8rem;
    line-height: 1.5rem;
    }
`;

export const SecondText = styled.h1`
font-family: 'M PLUS 1p', sans-serif;
font-style: normal;
font-weight: 400;
font-size: 1,5rem;
line-height: 2.5rem;
text-align: center;
color: #FFFFFF;
@media (max-width: 1024px) {
    font-size: 1.2rem;
    line-height: 2.5rem;
    }
@media (max-width: 768px) {
    font-size: 1.0rem;
    line-height: 2.2rem;
    }
@media (max-width: 426px) {
    font-size: 0.5rem;
    line-height: 1rem;
    }
`;

export const StyleSecondText = styled.div`
padding-top: 5rem;
width:50%;
display:flex;
justify-content:center;
margin-left:25%;
@media (max-width: 1024px) {
    padding-top: 3.5rem;
    }
@media (max-width: 768px) {
    padding-top: 1.5rem;
    }
@media (max-width: 426px) {
    padding-top: 0.2rem;
}
`;

export const StylePrincipalText = styled.div`

width:50%;
display:flex;
justify-content:center;
margin-left:25%;


`;


export const StyleButton = styled.div`
padding-top: 5rem;
display:flex;
justify-content:center;
@media (max-width: 768px) {
    padding-top: 1.5rem;
    }

`;

export const ButtonCarousel = styled.div`
width: 16.3rem;
height: 3.1rem;
background: #470606;
border-radius: 7px;
display: flex;
align-items: center;
justify-content: center;
@media (max-width: 1024px) {
    width: 12.3rem;
    height: 2.4rem;
    }
@media (max-width: 768px) {
    width: 11.3rem;
    height: 2.4rem;
    }
@media (max-width: 426px) {
    width: 6.3rem;
    height: 1.5rem;
}

`;

export const TextButton = styled.a`
font-family: 'M PLUS 1p', sans-serif;
font-style: normal;
font-weight: bold;
font-size: 0.8rem;
line-height: 1.3rem;
color: #FFFFFF;
:hover{
        color: #b9b9b9
    }
    @media (max-width: 1440px) {
    font-size: 1.0rem;
    line-height: 2.5rem;
    }
    @media (max-width: 1024px) {
    font-size: 0.8rem;
    line-height: 2.5rem;
    }
    @media (max-width: 768px) {
    font-size: 0.7rem;
    line-height: 2.5rem;
    }
    @media (max-width: 426px) {
    font-size: 0.5rem;
    line-height: 1.5rem;
    }
`;