import styled from 'styled-components';

export const Container = styled.div`
   
    width: 100%;
    height: 300vh;
    @media (max-width: 950px) {
        height: 400vh;
    }

`;


export const StyleContact = styled.div`
    


`;


export const StyleCards = styled.div`
    display: flex;
    align-items: center;
    justify-content:space-evenly;
    margin-top: 3%;

    @media (max-width: 950px) {
        flex-direction: column;
        padding: 1rem;
        height: 200vh;
        
    }
    @media (max-width: 767px) {
        flex-direction: column;
        padding: 1rem;
        height: 200vh;
        
    }
    @media (max-width: 426px) {
        flex-direction: column;
        padding: 0.5rem;
        height: 180vh;
    }
    

`;

export const TituloPrincipal = styled.h1`
    font-family: 'M PLUS 1p', sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 2rem;
    line-height: 4rem;
    color: #474747;
    @media (max-width: 768px) {
        font-size: 1.6rem;
    }
    @media (max-width: 426px) {
        font-size: 1.1rem;
    }
`;

export const TituloSecundario = styled.h3`
    font-family: 'M PLUS 1p', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 1rem;
    line-height: 2rem;
    color: #535353;
    @media (max-width: 768px) {
        font-size: 1.0rem;
    }
    @media (max-width: 426px) {
        font-size: 0.7rem;
    }
`;

export const StyleTitulos = styled.div`
    margin-top:5%;
    margin-left:6.5%;
`;

export const ButtonVerTodos = styled.div`
    width: 150px;
    height: 50px;
    background: #470606;
    border-radius: 7px;
    display:flex;
    align-items:center;
    justify-content:center;
`;

export const VerTodos = styled.a`
    font-family: 'M PLUS 1p', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 21px;
    color: #FFFFFF;
`;

export const StyleButton = styled.div`
    display:flex;
    justify-content:center;
    margin-top: 4%;
`;
