import styled from 'styled-components';

export const Container = styled.div`
    
    width: 14.5rem;
    height: 21.9rem;
    background-color:#fff;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    border-radius: 7px 7px 7px 7px;
    @media (max-width: 768px) {
        width: 12rem;
        height: 18.5rem;
    }
    @media (max-width: 426px) {
        width: 10rem;
        height: 16.5rem;
    }
    
`;

export const StyleItem = styled.div`
    padding:2%;

`;


export const StyleImg = styled.div`
    
    width: 100%;
    height: 151px;
    border-radius: 7px 7px 0px 0px;
    img{
        width: 14.5rem;
        height: 9.4rem;
        border-radius: 7px 7px 0px 0px;
        }
        @media (max-width: 768px) {
        img{
            width: 12rem;
            height: 9rem;
            border-radius: 7px 7px 0px 0px;
        }
    }
    @media (max-width: 426px) {
        width: 12rem;
        height: 7.1rem;
        img{
            width: 10rem;
            height: 7rem;
            border-radius: 7px 7px 0px 0px;
        }
    }
    
`;

export const TituloCard = styled.h1`

    font-family: 'M PLUS 1p', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 1rem;
    line-height: 21px;
    color: #000000;
    margin-top:0.45rem;
    margin-left:0.45rem;
    @media (max-width: 768px) {
        font-size: 0.7rem;
        margin-top:0.4rem;
        line-height: 1rem;
    }
    @media (max-width: 426px) {
        font-size: 0.7rem;
        margin-top:0.4rem;
        line-height: 1rem;
    }
`;

export const ParagrafoCard = styled.p`
    font-family: 'M PLUS 1p';
    font-style: normal;
    font-weight: 500;
    font-size: 0.9rem;
    line-height: 18px;
    margin-top:0.45rem;
    margin-left:0.45rem;
    @media (max-width: 768px) {
        font-size: 0.6rem;
        line-height: 0.8rem;
    }
    @media (max-width: 426px) {
        font-size: 0.6rem;
        line-height: 0.8rem;
    }
`;

export const SobreCard = styled.div`
    
    width: 2.75rem;
    height: 2.5rem;
    background: #470606;
    border-radius: 7px 0px;
    display:flex;
    align-items:center;
    justify-content:center;
    svg{
        color:#fff;
        font-size: 20px;
        cursor: pointer;
    :hover{
        color: #b9b9b9
    }
    }
    @media (max-width: 426px) {
        width: 2.2rem;
        height: 2.0rem;
        svg{
        color:#fff;
        font-size: 1.25rem;
        cursor: pointer;
    :hover{
        color: #b9b9b9
    }
    }
    }
`;

export const StyleCardSobre = styled.div`
    display: flex;
    flex-direction:row;
    width: 100%;
    justify-content:flex-end;
`;

