import styled from "styled-components";

export const Container = styled.div`
    height:10.7vh;
    @media (max-height: 1080px) {
        height:7.8vh;
    }
`;

export const StyleMenuView = styled.div`
    display:flex;

    justify-content:center;

`;

export const ItensMenu = styled.p`
    font-family: 'M PLUS 1p';
    font-style: normal;

    line-height: 1,5rem;
    border: none;
    padding:1rem;
    color: #ffff;
    background-color: Transparent;
    cursor: pointer;
    :hover{
        color: #b9b9b9
    }
    @media (max-width: 540px) {
        font-size: 0.7rem;
      }

`;
export const Menu = styled.div`
    width: 34rem;
    height: 3,4rem;
    background: #470606;
    border-radius: 0px 0px 7px 7px;
    font-size: 1rem;
    position: absolute;
    z-index: 12;
    margin-top: 5.3rem;
    display:flex;
    flex-direction:row;
    align-items: center;
    justify-content:center;
    position: fixed;
    @media (max-width: 768px) {
        width: 28rem;
        height: 3rem;
        font-size: 0.8rem;
      }
    @media (max-width: 540px) {
        width: 24rem;
        height: 2.5rem;
        font-size: 0.8rem;
      }
    @media (max-width: 390px) {
        font-size: 0.7rem;
      }
`;

export const View = styled.div`
    
    width: 100%;
    height: 5.3rem;
    background: #F9F3F3;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
    position: absolute;
    position: fixed;
    z-index: 12;
    
`;

export const DivWhats = styled.div`

    display: flex;
    align-items: center;
    flex-direction: row;

`;


export const DivImg = styled.div`
    display:flex;
    align-items:center;
    justify-content:space-around;
    svg {
        width: 2.2rem;
        height: 2.2rem;
        color: #470606;
        cursor: pointer;
    }
    @media (max-width: 540px) {
        svg{
            width: 1.8rem;
            height: 1.8rem;
        }
    @media (max-width: 400px) {
        svg{
            width: 1.8rem;
            height: 1.8rem;
        }
    }
`;

export const SvgHoverWhats = styled.div`
    display:flex;
    align-items:center;
    svg{
        :hover{
        color: #34af23
    }
    }
`;

export const SvgHoverFace = styled.div`
    display:flex;
    align-items:center;
    svg{
        :hover{
        color: #3b5998
    }
    }
`;

export const SvgHoverinsta = styled.div`
    display:flex;
    align-items:center;
    padding: 1.5rem;
    svg{
        :hover{
        color: #8134AF
    }
    }
    
`;

export const Link = styled.a`

`;

export const StyleIcons = styled.div`
    width: 25%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;

`


export const StyleMenu = styled.div`
    width: 100%;
    height: 55px;
    display:flex;
    justify-content:center;
    
    
`;

